.feedback {

	padding: rem(84) 0;

	> .container {
		width: 1220px;
		padding-left: rem(100);
		padding-right: rem(100);

		flex-wrap: wrap;
		flex-direction: row;
		justify-content: space-between;
	}

	.block_icon_text {
		display: flex;
	}

	&__left {
		display: block;
		width: rem(481);

		padding-top: rem(62);

		h2,
		>p,
		.feedback__tel,
		.feedback__email {
			display: block;
		}

		&-links {
			display: flex;
			flex-direction: column;

			.feedback__tel {
				font-size: rem(24);
				font-weight: 500;
				margin-bottom: rem(17);
				color: white;
			}

			.feedback__email {
				font-size: rem(20);
				color: white;
				margin-bottom: rem(17);
			}
		}

		h2 {
			color: white;
			margin-bottom: rem(23);
		}
		> p {
			color: white;
			font-size: rem(18);
			// padding-right: rem(60);
			margin-bottom: rem(56);
		}
	}

	&__right {
		width: rem(371);
	}

	// &__left, &__right {
	// 	flex: 0 0 50%;
	// 	max-width: 50%;
	// }

	&__form {
		// width: rem(371);
	}
}

.block_icon_text {
	p {
		font-size: rem(24);
	}
}