#header_store {
	height: rem(406);
	background: url(../img/page_store/header_bg_x3.jpg) no-repeat;
	background-size: cover;

	>.container {
		position: relative;

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: rem(54);
			width: rem(1202);
			height: rem(203);
			background: url(../img/page_store/header_i_x3.png) no-repeat top center;
		}
	}

	.header__bottom {
		// position: relative;
		margin-top: rem(98);

		h1 {
			font-size: rem(44);
			max-width: rem(620);
		}

	}
}