.partners {
	padding: rem(84) 0;

	h2 {
		margin-bottom: rem(52);
		max-width: rem(587);
		margin-left: auto;
		margin-right: auto;
	}

	&__items {
		width: rem(1000);
		margin-left: auto;
		margin-right: auto;

		.item {
			flex: 0 0 calc((100% - 16rem) / 5);
			max-width: calc((100% - 16rem) / 5);

			margin-bottom: 0;

			&:nth-child(4) {
				order: 3;
				margin-right: rem(63);
			}
		}

	}
}