.facts {
	padding: rem(64) 0;

	h2 {
		text-align: center;
		margin-bottom: rem(56);
		max-width: 100%;
	}

	&__item {
		position: relative;
		display: flex;
		align-items: center;

		.image {
			position: relative;
			z-index: 2;
			display: block;

			width: rem(384);
			height: rem(305);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.info {
			position: absolute;
			margin-left: rem(180);
			// z-index: ;

			background-color: var(--c-dark);
			padding: rem(26);
			&__text {
				position: relative;
				z-index: 3;
				background-color: white;
				padding: rem(40) rem(54);
				p {
					position: relative;
					font-size: rem(20);
					line-height: rem(31);
					color: #4E545A;
					padding-left: rem(75);

					&::before {
						content: "\e917";
						position: absolute;
						top: rem(10);
						left: 0;
						font-family: 'icons';
						font-size: rem(42);
						color: #151522;
					}
				}
			}
		}
	}

	.swiper-container {
		max-width: rem(800);
		margin: 0 auto rem(-48) auto;
		padding-bottom: rem(48);
	}

	.swiper-nav {
		position: absolute;
		// margin-top: rem(40);
		// height: rem(20);

		left: 50%;
		margin-left: rem(20);
		bottom: rem(58);
		z-index: 5;

		button {
			margin-right: rem(20);
			i {
				font-size: rem(20);
				&::before {
					font-size: rem(20);
					color: #30384B;
				}
			}
		}
		.swiper-button-disabled {

			i {
				font-size: rem(16);
				&::before {
					font-size: rem(16);
				}
			}
		}
	}
}