#header_info {
	height: rem(374);
	background: url(../img/page_info/header_bg_info_x3.jpg) no-repeat;
	background-size: cover;

	

	> .container {
		position: relative;

		&::before,
		&::after {
			content: "";
			position: absolute;
			pointer-events: none;
		}

		&::before {
			bottom: 0;
			left: rem(60);
			background: url(../img/page_info/header_i_left_x3.png) top center;
			background-size: 100%;
			width: rem(287);
			height: rem(288);
		}

		&::after {
			bottom: rem(20);
			right: rem(40);
			background: url(../img/page_info/header_i_right_x3.png) top center;
			background-size: 100%;
			width: rem(236);
			height: rem(265);
		}
	}

	h1 {
		font-size: rem(44);
		max-width: rem(790);
	}
}