.footer {
	padding-top: rem(76);

	&__top {
		padding-bottom: rem(48);

		> .flex {
			flex-direction: column;
			flex: 0 0 rem(380);
			justify-content: flex-start;
		}

		&-nav {
			flex: 0 0 auto;
			flex-grow: 1;

			margin-left: rem(147);
			
			.item {
				flex-grow: 1;
				flex: 0 0 auto;
			}

			.items {
				flex-grow: 1;
				flex: 0 0 auto;
			}
		}
	}

	&__bottom {
		padding: rem(13) 0;

		.links {
			justify-content: flex-end;
		}
	}
}