.news {
	display: flex;
	flex-wrap: wrap;

	padding-bottom: 0;

	&__top, &__bottom {
		flex: 0 0 50%;
	}

	&__top {
		margin-bottom: 0;
	}

	&__bottom {
		align-items: flex-start;
		justify-content: center;
		margin-left: rem(120);

		padding: rem(95) 0;

		h2 {
			margin-bottom: rem(16);
		}

		p {
			margin-bottom: rem(48);
		}

		.button_primary {
			align-self: flex-start;
		}
	}
}