.articles {


	&__items {
		justify-content: center;
		flex-wrap: wrap;
		flex-direction: row;
		.item {
			flex: 0 0 calc((100% - 4.5rem) / 3);
			max-width: calc((100% - 4.5rem) / 3);

			margin-right: rem(36);

			border-bottom: 0;

			&:last-child {
				margin-right: 0;
			}

			&__top {
				height: rem(190);
			}

		}

	}
}