.about_info {
	padding: rem(92) 0;

	>.container {
		display: flex;
		flex-wrap: wrap;
		width: calc(1220 - 3.75rem);
	}

	&__left {
		flex: 0 0 calc(50% - 3.75rem);
		max-width: calc(50% - 3.75rem);
		display: block;
		margin-right: rem(30);
	}

	&__right {
		flex: 0 0 calc(50% - 3.75rem);
		max-width: calc(50% - 3.75rem);
		h2 {
			margin-bottom: rem(28);
		}

		p {
			font-size: rem(15);
		}
	}


}