.slider {
	padding: rem(82) 0;

	h2 {
		margin-bottom: rem(56);
	}

	>.container {
		max-width: 1120px;
	}

	.swiper-container {
		max-width: 100%;

		.swiper-page {
			display: none;
		}

		.swiper-wrapper {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			.swiper-slide {
				width: rem(294);
			}
		}
	}
}