@function rem($px) {
	@return ($px / 16)+rem;
}

@mixin placeholder {
	&::-webkit-input-placeholder {
		@content;
	}

	&:-moz-placeholder {
		@content;
	}

	&::-moz-placeholder {
		@content;
	}

	&:-ms-input-placeholder {
		@content;
	}
}

@mixin hd {
	@media (min-width: 2480px) {
		@content;
	}
}

@mixin w1200_w900 {
	@media (max-width: 1200px) and (min-width: 900px) {
		@content;
	}
}

@mixin w768_w540 {
	@media (max-width: 768px) and (min-width: 540px) {
		@content;
	}
}

@mixin max {
	@media (min-width: 1680px) {
		@content;
	}
}

@mixin desktop {
	@media (min-width: 1200px) {
		@content;
	}
}

@mixin only_desktop {
	@media (min-width: 900px) {
		@content;
	}
