.benefits {
	padding-bottom: rem(84);

	&__top {
		margin-bottom: rem(52);
		max-width: rem(592);
	}

	&__bottom {
		padding-left: 0;
		padding-right: 0;

		&-items {
			margin-bottom: rem(48);

			.item {
				margin-bottom: 0;

				flex: 0 0 23%;
				max-width: 23%;
				margin-right: rem(32);

				&:last-child {
					margin-right: 0;
				}
				
			}
		}
	}
}