.our_values {
	padding: rem(84) 0 rem(42) 0;

	&__items {
		padding: 0;

		flex-wrap: wrap;
		flex-direction: row;

		.item {
			width: calc((100% - 4rem) / 3);
			padding: rem(28);
			margin-bottom: 0;

			margin-right: rem(32);

			&:last-child {
				margin-right: 0;
			}

			h3 {
				font-size: rem(18);
			}

			p {
				font-size: rem(13);
			}
		}
	}
}