.sertifications {
	padding: rem(84) 0;

	h2 {
		margin-bottom: rem(52);
	}

	&__items {
		margin-bottom: rem(48);
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: center;

		.item {
			margin-right: rem(36);
			margin-bottom: 0;

			&:last-child {
				margin-right: 0;
			}
		}
	}
}